import '../css/Common.scss';
import '../css/Detail.scss';
import {useState, useRef, useEffect } from "react";
import Content from "../../data/content.json";
import { useParams } from 'react-router-dom';
import { Link } from "react-router-dom";


export default function Detail( { contentOn, content2On, content3On, content4On, upIcon } ){

    let { num } = useParams();
    let [datade,setData] = useState(Content.portfolio);
    const liTags = useRef([]); 
    const pTags = useRef([]);
    const topH2 = useRef();
    const aaa = (e) => {
        const navList = liTags.current;
        const pImgs = pTags.current;
        const beb= e.target.dataset.nu;

        for(let i = 0; i < navList.length; i++){
            navList[i].classList.remove("on");
            pImgs[i].classList.remove("on");
        }
        navList[beb].classList.add("on");
        pImgs[beb].classList.add("on");
    }

    
    // btnTop start
    const moveUp = () => {
        topH2.current.scrollIntoView( {behavior: "smooth"} );
    }
    // btnTop end


    // pageNav start
    const movePrev = () => {
        if(num === "0") {
            return `${datade.length-1}`;
        }else{
            return `${Number(num)-1}`; 
        }
    }
    const moveNext = () => {
        if(num === `${datade.length-1}`) {
            return "0";
        }else{
            return `${Number(num)+1}`; 
        }
    }
    const [nextIndex, setnextIndex] = useState(0);
    const [prevIndex, setprevIndex] = useState(0);
    useEffect( () => {
        // pageNav 했을 때 위로
        topH2.current.scrollIntoView();

        if(num === `${datade.length-1}`) {
            setnextIndex(0);
        }else{
            setnextIndex( Number(num)+1 ); 
        }
        if(num === "0") {
            setprevIndex( datade.length-1 );
        }else{
            setprevIndex( Number(num)-1 ); 
        }
    }, [num]);
    // pageNav end


    return (
        <div className="projectDetail" style={{backgroundImage: `url(${datade[num]['detitimg']})`}}>

            <div className="proAll all" id="wrapper">
                <div className="container">

                    <div className="projectHead">
                        <div className="close">
                            <Link to="/Project">
                                프로젝트로 가기
                            </Link>
                        </div>
                        <h2 ref={topH2} dangerouslySetInnerHTML={{__html: datade[num]['detitle']}}></h2>
                        <ul className="nav focus show">
                            <li data-nu="0" ref={element => { liTags.current[0] = element }} onClick={ (e) => { aaa(e) } } className="pc on">
                                <a data-nu="0" >PC WEB</a>
                            </li>
                            <li data-nu="1" ref={element => { liTags.current[1] = element }} onClick={ (e) => { aaa(e) } } className="mobile">
                                <a data-nu="1" >MOBILE</a>
                            </li>
                            <li data-nu="2" ref={element => { liTags.current[2] = element }} onClick={ (e) => { aaa(e) } } className="tablet">
                                <a data-nu="2" >TABLET</a>
                            </li>
                        </ul>
                        <div className="visual focus show">
                            <p ref={element => { pTags.current[0] = element }} className="pc on">
                                <img src={datade[num]['vipcimg']} />
                            </p>
                            <p ref={element => { pTags.current[1] = element }} className="mobile">
                                <img src={datade[num]['vimoimg']} />
                            </p>
                            <p ref={element => { pTags.current[2] = element }} className="tablet">
                                <img src={datade[num]['vitaimg']} />
                            </p>
                        </div>
                        <div className="site">
                            <p className="btn">
                                <a href={datade[num]['url']} target="blank">
                                    <span className="date">{datade[num]['date']}</span>
                                    <br />
                                    <strong>
                                        <span>SITE URL</span>
                                    </strong>
                                </a>
                            </p>
                        </div>
                    </div>
                    <div className="feature">
                        <div className="wrap">
                            <h3 className={ contentOn ? "typeB focus show" : "typeB focus"}>
                                <span>
                                    PROJECT
                                    <span className={ contentOn ? "br focus show" : "br focus"}></span>
                                    FEATURE
                                </span>
                            </h3>
                            <dl className={ contentOn ? "focus show" : "focus"}>
                                <div class="fl">
                                    <dt>FIELD</dt>
                                    <dd>{datade[num]['field']}</dd>
                                    <dt>SKILL</dt>
                                    <dd dangerouslySetInnerHTML={{__html: datade[num]['skill']}}></dd>
                                </div>
                                <div class="fr">
                                    <dt>PARTICIPATION</dt>
                                    <dd>{datade[num]['percent']}</dd>
                                    <dt>MAIN FUNCTION</dt>
                                    <dd>{datade[num]['function']}</dd>
                                </div>
                            </dl>
                            <div className="overview">
                                <h3 className="typeA">
                                    <span className={ contentOn ? "focus show" : "focus"}>
                                        <span>O</span>
                                        <span>V</span>
                                        <span>E</span>
                                        <span>R</span>
                                        <span>V</span>
                                        <span>I</span>
                                        <span>E</span>
                                        <span>W</span>
                                    </span>
                                </h3>
                                <p className={ contentOn ? "txt focus show" : "txt focus"}  dangerouslySetInnerHTML={{__html: datade[num]['overtxt']}}></p>
                            </div>
                            <div className="concept">
                                <p className={ contentOn ? "pic focus show" : "pic focus"}>
                                    <img src={datade[num]['conceptimg']} alt="없엉" />
                                </p>
                            </div>
                        </div>
                    </div>
                    <div className="flow" style={{backgroundColor:datade[num]['flowbg']}}>
                        <div className="wrap">
                            <h3 className="typeA">
                                <span className={ content2On ? "focus show" : "focus"}>
                                    <span>C</span>
                                    <span>O</span>
                                    <span>N</span>
                                    <span>T</span>
                                    <span>I</span>
                                    <span>N</span>
                                    <span>U</span>
                                    <span>O</span>
                                    <span>U</span>
                                    <span>S</span>
                                </span>
                                <span className={ content2On ? "br focus show" : "br focus"}></span>
                                <span className={ content2On ? "focus show" : "focus"}>
                                    <span>F</span>
                                    <span>L</span>
                                    <span>O</span>
                                    <span>W</span>
                                </span>
                            </h3>
                            <div className={ content2On ? "detail focus show" : "detail focus"}>
                                <h4>DETAIL</h4>
                                <p>
                                    <span>
                                        <img src={datade[num]['descrollimg']} alt="없엉" />
                                    </span>
                                </p>
                            </div>
                            <div className="grid">
                                <p className={ content2On ? "caution focus show" : "caution focus"}>
                                    <strong>12</strong>
                                    <br />
                                    Grid System
                                </p>
                                <ul>
                                    <li className={ content2On ? "focus show" : "focus"}>
                                        <img src={datade[num]['deflowleft']} alt="없엉"></img>
                                    </li>
                                    <li className={ content2On ? "focus show" : "focus"}>
                                        <img src={datade[num]['deflowright']} alt="없엉"></img>
                                    </li>
                                </ul>
                            </div>
                        </div>
                    </div>
                    <div className="discover all">
                        <div className="wrap">
                            <h3 className="typeA">
                                <span className={ content3On ? "focus show" : "focus"}>
                                    <span>D</span>
                                    <span>I</span>
                                    <span>S</span>
                                    <span>C</span>
                                    <span>O</span>
                                    <span>V</span>
                                    <span>E</span>
                                    <span>R</span>
                                </span>
                                <span className={ content3On ? "br focus show" : "br focus"}></span>
                                <span className={ content3On ? "focus show" : "focus"}>
                                    <span>D</span>
                                    <span>E</span>
                                    <span>S</span>
                                    <span>I</span>
                                    <span>G</span>
                                    <span>N</span>
                                </span>
                            </h3>
                            <div className="deviceArea">
                                <p className={ content3On ? "device1 focus show" : "device1 focus"}>
                                    <img src={datade[num]['device1']} alt="pc" />
                                </p>
                                <p className={ content3On ? "device2 focus show" : "device2 focus"}>
                                    <img src={datade[num]['device2']} alt="left" />
                                </p>
                                <p className={ content3On ? "device3 focus show" : "device3 focus"}>
                                    <img src={datade[num]['device3']} alt="right" />
                                </p>
                                <p className={ content3On ? "device4 focus show" : "device4 focus"}>
                                    <img src={datade[num]['device4']} alt="tablet" />
                                </p>
                            </div>
                            <div className={ content3On ? "txtArea focus typeA show" : "txtArea focus typeA"}>
                                <span>{datade[num]['span1']}</span>
                                <br />
                                <span>{datade[num]['span2']}</span>
                            </div>
                        </div>
                    </div>
                    <div className="flowMobile">
                        <div className="wrap">
                            <h3 className="typeA">
                                <span className={ content4On ? "focus show" : "focus"}>
                                    <span>C</span>
                                    <span>O</span>
                                    <span>N</span>
                                    <span>T</span>
                                    <span>I</span>
                                    <span>N</span>
                                    <span>U</span>
                                    <span>O</span>
                                    <span>U</span>
                                    <span>S</span>
                                </span>
                                <span className={ content4On ? "br focus show" : "br focus"}></span>
                                <span className={ content4On ? "focus show" : "focus"}>
                                    <span>F</span>
                                    <span>L</span>
                                    <span>O</span>
                                    <span>W</span>
                                    <span>M</span>
                                    <span>O</span>
                                    <span>B</span>
                                    <span>I</span>
                                    <span>L</span>
                                    <span>E</span>
                                </span>
                            </h3>
                            <div className={ content4On ? "detail focus show" : "detail focus"}>
                                <h4>DETAIL</h4>
                                <p>
                                    <span>
                                        <img src={datade[num]['descrollmoimg']} alt="없엉" />
                                    </span>
                                </p>
                            </div>
                            <ul>
                                <li className={ content4On ? "focus show" : "focus"}>
                                    <img src={datade[num]['demflowleft']} className ={ (datade[num]['demflowleft'] == undefined) ? "have hidden" : "have"} alt="없엉"></img>
                                </li>
                                <li className={ content4On ? "focus show" : "focus"}>
                                    <img src={datade[num]['demflowright']} className ={ (datade[num]['demflowright'] == undefined) ? "have hidden" : "have"} alt="없엉"></img>
                                </li>
                            </ul>
                        </div>
                    </div>
                    <div className="custom">
                        <div className="wrap">
                            <h3 className="typeA">
                                <span className={ content4On ? "focus show" : "focus"}>
                                    <span>C</span>
                                    <span>O</span>
                                    <span>L</span>
                                    <span>O</span>
                                    <span>R</span>
                                </span>
                            </h3>
                            <ul className={ content4On ? "colorChip focus show" : "colorChip focus"}>
                                <li>
                                    <span className="color" style={{backgroundColor:datade[num]['colorbgfir']}}></span>
                                    {datade[num]['firtxt']}
                                    <br />
                                    {datade[num]['colorbgfir']}
                                </li>
                                <li>
                                    <span className="color" style={{backgroundColor:datade[num]['colorbgsec']}}></span>
                                    {datade[num]['sectxt']}
                                    <br />
                                    {datade[num]['colorbgsec']}
                                </li>
                                <li>
                                    <span className="color" style={{backgroundColor:datade[num]['colorbgthi']}}></span>
                                    {datade[num]['thitxt']} 
                                    <br />
                                    {datade[num]['colorbgthi']}
                                </li>
                                <li>
                                    <span className="color" style={{backgroundColor:datade[num]['colorbgfor']}}></span>
                                    {datade[num]['fortxt']}
                                    <br />
                                    {datade[num]['colorbgfor']}
                                </li>
                            </ul>
                        </div>
                    </div>
                    <ul className="pageNav">
                        <li className="first">
                            <Link to={movePrev} >
                                {datade[prevIndex].listtxt}
                                <br />
                                {datade[prevIndex].listtxts}
                            </Link>
                        </li>
                        <li className="second">
                            <Link to={moveNext}>
                                {datade[nextIndex]["listtxt"]}
                                <br />
                                {datade[nextIndex]["listtxts"]}
                            </Link>
                        </li>
                    </ul>
                </div>
                <p onClick={moveUp} className={ upIcon ? "btnTop show" : "btnTop"}>
                    <a herf="#">
                        <em>
                            <span>
                                TOP
                            </span>
                        </em>
                    </a>
                </p>
            </div>
            
        </div>
    );
}
