import '../../App.scss';
import '../css/Main.scss';
import '../css/About.scss';
import Skill from './Skill';
import React, { useState, useEffect, useRef } from "react";

const About = ( { setLine1, topOnChange, topOn, changeWh, setSlideState, verticalWh, vertical } ) => {

    document.body.style = "height:10000px;"

    let y = 0,inter, topNum=0;
    let [inNum, setInNum] = useState(-1);
    
    const upTxt  = useRef([]);
    const upTxtState = ["on","","",""];
    const nav = document.getElementsByClassName('nav');
    const me = document.getElementsByClassName('introPho');
    const meme = document.getElementsByClassName('introPho2');
    
    let scrollPos = {y:0,y2:0,state:true}
    function scrollUpDown(){
        scrollPos.y = window.scrollY;
        scrollPos.state = scrollPos.y > scrollPos.y2 ? true : false;
        scrollPos.y2 = scrollPos.y;
    }

    const con = useRef([]);
    const aaa = useRef([]);
    const contents = con.current;
    const navTag = aaa.current;
    // const back = useRef();



    let a = 0,blen=true;
    const scrollHandle = (e) => {

        
        let sPos = document.body.offsetHeight / 4;

        scrollUpDown();
        // const backLine = back.current;
        clearTimeout(inter);
        inter = setTimeout(()=>{
        if(blen) {
            blen=false;
            setSlideState(false)
            // changeWh(false)
    
            for(let i=0;i<4;i++){
                navTag[i].classList.remove("on");
                contents[i].classList.remove("up", "show");
                // backLine[i].classList.remove("line");
            }

            if(!scrollPos.state){
                // console.log("scroll Up");
                topOnChange(false);
                if(inNum == 0){
                    vertical(false);
                    setLine1(false);
                    // changeWh(true); 선생님이 하신거
                } 
                if(inNum != -1) setInNum(inNum-=1);
            }else{
                // console.log("scroll Down");
                // changeWh(false); 선생님이 하신거
                vertical(true);
                setLine1(true);
                if(inNum < 3) setInNum(inNum+=1);
            }

            (inNum == -1) ? topNum = '100%' : topNum = '240px';
            nav[0].style=`top:${topNum}`;

            
            window.scrollTo(0,2000*(inNum+1));
            console.log(inNum)
           
            setTimeout(()=>{blen=true},500)


            if(inNum == -1) {
                setSlideState(true); 
                // bl ? changeBl(false) : changeBl(true);                
                return;
            };
            navTag[inNum].classList.add("on");
            contents[inNum].classList.add("up", "show");

           
            
        }
         
        },400)
    }
   
    useEffect( () => {
        // about.current.style = "height:2000px";
        setTimeout(()=>{window.scrollTo(0,0);},200);
        window.addEventListener("scroll", scrollHandle);
        // window.addEventListener("wheel", wheelHandle);
        return () => {
            window.removeEventListener("scroll", scrollHandle);
            // window.removeEventListener("wheel", wheelHandle);
        }
    },[])

    return (
        <>
            
            <div className="about" id="mainSec_2">
                <div className="nav" style={ topOn ?  {top: "240px"} : {}}>
                    <ul>
                        <li ref={ bbb => aaa.current[0] = bbb } className={topOn ? `aaa on` : "aaa"}>
                            <a href="#intro">Introduce </a>
                        </li>
                        <li ref={ bbb => aaa.current[1] = bbb } className={`aaa`}>
                            <a href="#info">Info / Skill</a>
                        </li>  
                        <li ref={ bbb => aaa.current[2] = bbb } className={`aaa`}>
                            <a href="#etc">Etc</a>
                        </li>   
                        <li ref={ bbb => aaa.current[3] = bbb } className={`aaa`}>
                            <a href="#contact">Contact</a>
                        </li>   
                    </ul>
                </div>
                
                <div ref={ item => con.current[0] = item } className={topOn ? "content focus up show" : "content focus"} id="intro" style={ topOn ?  {top: "0"} : {}}>
                    <div className="introCon">
                        <div className="inTxt focus">
                            <p>
                                <span>
                                    코드를 디자인하다
                                </span>
                            </p>
                            <p>
                                <span>
                                    웹퍼블리셔 <strong>이지현</strong>입니다
                                </span>
                            </p>
                        </div>
                        <p className="inCon focus">
                            WEB은 '나'를 표현할 수 있는 또 하나의 매개체라고 생각합니다.<br />
                            웹페이지 또한 기업, 상품의 얼굴을, <br />
                            즉 '나'의 브랜드 이미지를 사용자에게 각인시킬 수 있는 하나의 공간을 만드는 것에 매력을 느꼈습니다.<br /><br />
                            잘 만든 작업물은 탄탄한 기획과 협업자들의 원활한 커뮤니케이션에서 나온다고 생각합니다.<br />
                            함께 의견을 조율, 상의하며 웹이라는 공간에 대상의 이야기를 담는다는 마음으로 창의적인 공간을 만들고 싶습니다.<br />
                            섬세한 기획과 비즈니스 커뮤니케이션을 통해 디자인과 코딩을 하는 웹퍼블리셔가 되겠습니다.
                        </p>
                    </div>
                    <div className="introPho focus">
                        <img src="./img/port/me.png" alt="없엉" />
                    </div>
                </div>

                <div ref={ item => con.current[1] = item }  className="content focus" id="info">
                    <Skill />
                </div>

                <div ref={ item => con.current[2] = item } className="content focus" id="etc">
                    <div className="wrap">
                        <h3 className="typeA">
                            <span className="focus">
                                <span>I</span>
                                <span>N</span>
                                <span>T</span>
                                <span>E</span>
                                <span>R</span>
                                <span>I</span>
                                <span>O</span>
                                <span>R</span>
                            </span>
                            <span className="br focus"></span>
                            <span className="focus">
                                <span>D</span>
                                <span>E</span>
                                <span>S</span>
                                <span>I</span>
                                <span>G</span>
                                <span>N</span>
                            </span>
                        </h3>
                        <div className="detail focus">
                            <h4>DETAIL</h4>
                            <p>
                                <span>
                                    <img src="./img/main/about.jpg" alt="없엉" />
                                </span>
                            </p>
                        </div>
                    </div>
                </div>

                <div ref={ item => con.current[3] = item } className="content focus" id="contact">
                    <div className="contactUp">
                        <div className="contactCon">
                            <h1>CONTACT</h1>
                            <h2 className="small">get in touch</h2>
                            <div className="layoutWrap">
                                <div className="layout">
                                    <div className="layoutLow">
                                        <div className="layoutL">
                                            <div className="leftTxt">
                                                <h2>don't be shy</h2>
                                                <p>Feel free to get in touch with me. I am always open to discussing new projects, creative ideas or opportunities to be part of your visions.</p>
                                            </div>
                                            <div className="click">
                                                <span>click</span> &nbsp;
                                                <span>icon</span>
                                                <span> → </span>
                                            </div>
                                        </div>
                                        <div className="layoutR">
                                            <div className="eMail">
                                                <a href="mailto:elgusdl14@naver.com" target="_blank" className="eImg">
                                                    <img src="./img/contact/6.png" />
                                                </a>
                                                <div className="txt">
                                                    <h4>E-Mail</h4>
                                                    <p>elgusdl14@naver.com</p>
                                                </div>
                                            </div>
                                            <div className="git">
                                                <a href="https://github.com/elgusdl" target="_blank" className="gImg">
                                                    <img src="./img/contact/1.png" />
                                                </a>
                                                <div className="txt">
                                                    <h4>Git</h4>
                                                    <p>github.com/elgusdl</p>
                                                </div>
                                            </div>
                                            <div className="phone">
                                                <div className="pImg">
                                                    <img src="./img/contact/2.png" />
                                                </div>
                                                <div className="txt">
                                                    <h4>HP</h4>
                                                    <p>010.3639.5409</p>
                                                </div>
                                            </div>
                                            <div className="kakao">
                                                <a href="https://open.kakao.com/o/s0Ym34Fd" target="_blank" className="kImg">
                                                    <img src="./img/contact/3.png" />
                                                </a>
                                                <div className="txt">
                                                    <h4>Kakao</h4>
                                                    <p>elgusdl</p>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="contactDawn">
                        <div className="dawn">
                            <p>2021 웹퍼블리셔 이지현.</p>
                            <p>본 사이트는 개인 포트폴리오 사이트로 제작되어 상업적 목적이 아닙니다. 사용된 일부 이미지 및 폰트 등은 별도의 출처가 있음을 밝혀 드립니다.</p>
                        </div>
                    </div>
                </div>
            </div>
        </>
    );
};

export default About;