import { BrowserRouter, Route, Switch, Link } from "react-router-dom";
import './App.scss';
import './compo/css/Common.scss';
import Main from './compo/js/Main';
import About from './compo/js/About';
import Project from './compo/js/Project';
import Detail from "./compo/js/Detail";
import { useState, useEffect, useRef } from "react";
import Header from "./compo/js/Header";


function useScroll() {
  const [scrollY, setScrollY] = useState(0);
  const [contentOn, setContentOn] = useState(false);
  const [content2On, setContent2On] = useState(false);
  const [content3On, setContent3On] = useState(false);
  const [content4On, setContent4On] = useState(false);
  const [upIcon, setUpIcon] = useState(false);

  const listener = () => {
    if( window.scrollY > 300 && window.scrollY < 1600){
      setContentOn(true);
    }else{
      setContentOn(false);
    }
    if( window.scrollY > 1500 && window.scrollY < 7000){
      setContent2On(true);
    }else{
      setContent2On(false);
    }
    if( window.scrollY > 4500 && window.scrollY < 7400){
      setContent3On(true);
    }else{
      setContent3On(false);
    }
    if( window.scrollY > 5500 && window.scrollY < 16000){
      setContent4On(true);
    }else{
      setContent4On(false);
    }

    if( window.scrollY > 300){
      setUpIcon(true);
    }else{
      setUpIcon(false);
    }
  };

  useEffect(() => {
      window.addEventListener("scroll", listener);
      return () => {
          window.removeEventListener("scroll", listener);
      };
  });

  return { scrollY, contentOn, content2On, content3On, content4On, upIcon };
}







function App() { 
  
  
  const { scrollY, contentOn, content2On, content3On, content4On, upIcon } = useScroll();
  const [wh, setWh] = useState(true); // 수평
  const [verticalWh, setVerticalWh] = useState(true); // 수직
  const [slide, setSlide] = useState(true);
  const [topOn, setTopOn] = useState(false);
  const [access, setAccess] = useState(true);
  const [line1, setLine1] = useState(false);
  const [line2, setLine2] = useState(false);
  const accessChange = (bol) => {
    setAccess(bol);
}
  const topOnChange = (bol) => {
    setTopOn(bol);
  }
  const setSlideState = (s) => {
    setSlide(s);
  }
  const changeWh = (color) => {
    setWh(!color);
  }
  const vertical = (wh) => {
    setVerticalWh(!wh);
  }
  const bol_false = false;
  const wh_white = (bol=false) => {
    setWh(bol);
  }

  return (

    <main>
    <BrowserRouter basename="/ljhportfolio">
      <div className="s1" id="s1">
        <Switch>
          <Route exact path="/"> 
            <Header line1={line1} setLine1={setLine1} line2={line2} setLine2={setLine2} access={access} accessChange={accessChange} vertical={vertical} topOnChange={topOnChange} wh={wh} verticalWh={verticalWh} wh_white={wh_white} /> <Main scrollY={scrollY} slideState={slide} vertical={vertical} verticalWh={verticalWh} changeWh={changeWh}/>
            <About setLine1={setLine1} topOnChange={topOnChange} vertical={vertical} topOn={topOn} wh={wh} changeWh={changeWh} verticalWh={verticalWh} setSlideState={setSlideState}/> 
          </Route>
          {/* <Route path="/About"> <Header setLine2={setLine2} access={access} accessChange={accessChange} vertical={vertical} topOnChange={topOnChange} wh={wh} verticalWh={verticalWh} wh_white={wh_white} /> <About setLine2={setLine2} topOnChange={topOnChange} vertical={vertical} topOn={topOn} wh={wh} changeWh={changeWh} verticalWh={verticalWh} setSlideState={setSlideState} /> </Route> */}
          <Route path="/Project"> <Header line1={line1} setLine1={setLine1} line2={line2} setLine2={setLine2} access={access} accessChange={accessChange} vertical={vertical} wh={bol_false} wh_white={wh_white} topOnChange={topOnChange}  /> <Project /> </Route>
          <Route path="/Detail/:num"> <Detail upIcon={upIcon} contentOn={contentOn} content2On={content2On} content3On={content3On} content4On={content4On}/> </Route>
        </Switch>
      </div>
    </BrowserRouter>
    </main>

  );
}

export default App;
