import '../css/Common.scss';
import '../css/Project.scss';
import { Link } from "react-router-dom";
import {useEffect, useState} from "react";
import Content from "../../data/content.json";
import { useHistory } from "react-router-dom";

export default function Project(){

    document.body.style = "height:0;"
    
    let history = useHistory();
    let [data,setData] = useState(Content.portfolio);
    useEffect( () => {
        window.scrollTo(0, 0);
        history.push('/Project');
    }, []);

    return (
        <div className="listCon" id="listCon">
            
            <div className="subNav" id="subNav">
                <ul>
                    <li>
                        <div className="tit show">
                            <p>Creative</p>
                            <span></span>
                        </div>
                    </li>
                    <li className="history">
                        <a href="#">2021.06 ~ 10</a>
                    </li>
                </ul>
            </div>
            <div className="proList">
                <ul>
                    {
                        data.map(function(dataList,k){
                            return(
                                <li key={k}>
                                    <Link to={`/Detail/${k}`}>
                                        <img src={dataList['listsum']} alt="nop" />
                                        <div className="deta">
                                            <p className="txt">
                                                {dataList['listtxt']}
                                                <br />
                                                <strong>{dataList['listtxts']}</strong>
                                            </p>
                                            <p className="link">Web / Mobile / Tablet</p>
                                        </div>
                                    </Link>
                                </li>
                            );
                        })
                    }
                </ul>
            </div>
        </div>
    );
}