import { Link, useLocation } from "react-router-dom";
import '../../App.scss';
import '../css/Common.scss';
import { useEffect} from "react";

const Header = ( { line1, setLine1, line2, setLine2, access, accessChange, vertical, topOnChange, wh, wh_white, verticalWh } ) => {
    
    // header menu animation start
    // const [line1, setLine1] = useState(false);
    // const [line2, setLine2] = useState(false);
    let loc = useLocation();
   
    const clicktome = () => {
            topOnChange(true);
            vertical(true);
            setLine1(true);
            accessChange(false);
    }
    // const logoClick = () => {
        // window.scrollTo(0, 0);
        // if(location.pathname == "/") return;
    //     topOnChange(false);
    //     vertical(false);
    //     setLine1(false);
    //     wh_white(true);
    //     accessChange(false);
    // }
    useEffect(() => {
        if(loc.pathname == "/"){
            setLine1(true);
            topOnChange(true);
            vertical(true);
        }else{
            setLine1(false);
        }
        if(loc.pathname == "/Project"){
            setLine2(true);
            setLine1(false);
        }else{
            setLine2(false);
        }
        if(loc.pathname == "/"){
            setLine1 && setLine2(false);
        }
    }, [loc.pathname]);


    useEffect( () => {
        if(access){
            setLine1(false);
            topOnChange(false);
            vertical(false);
        }
    }, [access])


    return (
        <div className="header" id="header">
            <h1>
                <Link to="/">
                    <img onClick={() =>  window.location.replace("/ljhportfolio")} src={ verticalWh ? (wh ? "./img/main/logob.png" :  "./img/main/logow.png") : "./img/main/logob.png" } alt="로고없엉" /> 
                </Link>
            </h1>
            <div className="menuBtn">
                <div className="menu" id="menu">
                <ul>
                    <li className={ line1 ? "line" : ""}>
                        <Link  to="/" onClick={clicktome} className={ verticalWh ? (wh ? "" :  "white") : ""} >ABOUT</Link>
                    </li>
                    <li className={ line2 ? "line" : ""}>
                        <Link to="/Project" onClick={() => accessChange(false)} className={ verticalWh ? (wh ? "" :  "white") : ""}>PROJECT</Link>
                    </li>
                    <li>
                        <a href="https://www.instagram.com/2elgusdl/" target="_blank" className={ verticalWh ? (wh ? "" :  "white") : ""}>SNS</a>
                    </li>
                </ul>
                </div>
            </div>
        </div>
    )
}

export default Header;